          {{ errorMessage }}
        </div>
      </div>

      <!-- è½¬æ¢ç»“æœ -->
      <div class="results-section" v-if="inputValue && !hasError">
        <h3>ğŸ”„ è½¬æ¢ç»“æœ</h3>
        
        <!-- ç»“æœå¡ç‰‡ -->
        <div class="result-cards">
          <div 
            v-for="base in availableBases" 
            :key="base"
            class="result-card"
            :class="{ 'active': base === inputBase }"
          >
            <div class="result-header">
              <span class="result-title">{{ getBaseName(base) }}</span>
              <span class="result-suffix">{{ getBaseSuffix(base) }}</span>
