              <el-button 
                size="mini" 
                type="primary" 
                @click="copyToClipboard(getConvertedValue(base))"
              >
                <i class="el-icon-copy-document"></i> å¤åˆ¶
              </el-button>
              <el-button 
                size="mini" 
                @click="useAsInput(base, getConvertedValue(base))"
                v-if="base !== inputBase"
              >
                <i class="el-icon-refresh"></i> ä½œä¸ºè¾“å…¥
              </el-button>
            </div>
          </div>
        </div>

        <!-- è¯¦ç»†ä¿¡æ¯ -->
        <div class="details-section">
          <h4>ğŸ“Š è¯¦ç»†ä¿¡æ¯</h4>
          <div class="detail-grid">
            <div class="detail-item">
              <span class="detail-label">åŸå§‹è¾“å…¥ï¼š</span>
              <span class="detail-value">{{ inputValue }} ({{ getBaseName(inputBase) }})</span>
            </div>
            <div class="detail-item">
              <span class="detail-label">åè¿›åˆ¶å€¼ï¼š</span>
              <span class="detail-value">{{ decimalValue }}</span>
            </div>
            <div class="detail-item">
              <span class="detail-label">æ˜¯å¦ä¸ºæ•´æ•°ï¼š</span>
              <span class="detail-value">{{ isInteger ? 'æ˜¯' : 'å¦' }}</span>
            </div>
            <div class="detail-item">
              <span class="detail-label">ä½æ•°/é•¿åº¦ï¼š</span>
              <span class="detail-value">{{ inputValue.replace('.', '').length }} ä½</span>
            </div>
