          <h4>ğŸ” è½¬æ¢æ­¥éª¤</h4>
          <div class="conversion-steps">
            <div v-for="(step, index) in conversionSteps" :key="index" class="step-item">
              <div class="step-number">{{ index + 1 }}</div>
              <div class="step-content">{{ step }}</div>
