        <div class="empty-text">è¯·è¾“å…¥è¦è½¬æ¢çš„æ•°å€¼</div>
        <div class="empty-tips">
          <p>æ”¯æŒåŠŸèƒ½ï¼š</p>
          <ul>
            <li>âœ… äºŒè¿›åˆ¶ã€å…«è¿›åˆ¶ã€åè¿›åˆ¶ã€åå…­è¿›åˆ¶äº’è½¬</li>
            <li>âœ… æ”¯æŒå°æ•°è½¬æ¢</li>
            <li>âœ… å®æ—¶è½¬æ¢å’ŒéªŒè¯</li>
            <li>âœ… è¯¦ç»†çš„è½¬æ¢æ­¥éª¤</li>
            <li>âœ… ä¸€é”®å¤åˆ¶ç»“æœ</li>
          </ul>
        </div>
      </div>
    </div>

    <!-- å¿«æ·è½¬æ¢ -->
    <div class="quick-convert-section">
      <h3>âš¡ å¿«æ·è½¬æ¢</h3>
      <div class="quick-buttons">
        <el-button 
          v-for="example in quickExamples" 
          :key="example.value"
          size="small" 
          @click="setQuickExample(example)"
        >
          {{ example.label }}
        </el-button>
      </div>
    </div>

    <!-- è®¾ç½®é€‰é¡¹ -->
    <div class="settings-section">
