      <div class="setting-controls">
        <div class="setting-item">
          <el-checkbox v-model="showSteps">æ˜¾ç¤ºè½¬æ¢æ­¥éª¤</el-checkbox>
        </div>
        <div class="setting-item">
          <el-checkbox v-model="upperCaseHex">åå…­è¿›åˆ¶ä½¿ç”¨å¤§å†™å­—æ¯</el-checkbox>
        </div>
        <div class="setting-item">
          <el-checkbox v-model="showPrefix">æ˜¾ç¤ºè¿›åˆ¶å‰ç¼€ (0b, 0o, 0x)</el-checkbox>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ClipboardUtil } from '@/util/ClipboardUtil'

export default {
  name: 'BaseConverterView',
  data() {
    return {
      inputValue: '',
      inputBase: '10',
      hasError: false,
      errorMessage: '',
      showSteps: false,
      upperCaseHex: false,
      showPrefix: false,
      
      availableBases: ['2', '8', '10', '16'],
      
      quickExamples: [
        { label: '255 (åè¿›åˆ¶)', value: '255', base: '10' },
        { label: '1010 (äºŒè¿›åˆ¶)', value: '1010', base: '2' },
        { label: '377 (å…«è¿›åˆ¶)', value: '377', base: '8' },
        { label: 'FF (åå…­è¿›åˆ¶)', value: 'FF', base: '16' },
        { label: '3.14 (åè¿›åˆ¶)', value: '3.14', base: '10' },
        { label: '1100.1 (äºŒè¿›åˆ¶)', value: '1100.1', base: '2' }
      ]
    }
  },
  computed: {
    decimalValue() {
      if (!this.inputValue || this.hasError) return 0
      return this.convertToDecimal(this.inputValue, parseInt(this.inputBase))
    },
    
    isInteger() {
      return Number.isInteger(this.decimalValue)
    },
    
    conversionSteps() {
      if (!this.inputValue || this.hasError) return []
      return this.generateConversionSteps()
    }
  },
  methods: {
    handleInputChange() {
      this.validateInput()
    },
    
    handleInputBaseChange() {
      this.validateInput()
    },
    
    validateInput() {
      if (!this.inputValue.trim()) {
        this.hasError = false
        this.errorMessage = ''
        return
      }
      
      const base = parseInt(this.inputBase)
      const allowedChars = this.getAllowedCharsRegex(base)
      
      if (!allowedChars.test(this.inputValue)) {
        this.hasError = true
        this.errorMessage = `è¾“å…¥åŒ…å«ä¸å…è®¸çš„å­—ç¬¦ã€‚${this.getBaseName(this.inputBase)}åªå…è®¸ä½¿ç”¨ï¼š${this.getAllowedChars(this.inputBase)}`
        return
      }
      
      // æ£€æŸ¥å°æ•°ç‚¹
      const dotCount = (this.inputValue.match(/\./g) || []).length
      if (dotCount > 1) {
        this.hasError = true
        this.errorMessage = 'æ•°å€¼ä¸­åªèƒ½åŒ…å«ä¸€ä¸ªå°æ•°ç‚¹'
        return
      }
      
      this.hasError = false
      this.errorMessage = ''
    },
    
    getBaseName(base) {
      const names = {
        '2': 'äºŒè¿›åˆ¶',
        '8': 'å…«è¿›åˆ¶',
        '10': 'åè¿›åˆ¶',
        '16': 'åå…­è¿›åˆ¶'
      }
      return names[base] || ''
    },
    
    getBaseSuffix(base) {
      const suffixes = {
        '2': 'â‚‚',
        '8': 'â‚ˆ',
        '10': 'â‚â‚€',
        '16': 'â‚â‚†'
      }
      return suffixes[base] || ''
    },
    
    getAllowedChars(base) {
      const chars = {
        '2': '0, 1, .',
        '8': '0-7, .',
        '10': '0-9, .',
        '16': '0-9, A-F (æˆ– a-f), .'
      }
      return chars[base] || ''
    },
    
    getAllowedCharsRegex(base) {
      const patterns = {
        2: /^[01]*\.?[01]*$/,
        8: /^[0-7]*\.?[0-7]*$/,
        10: /^[0-9]*\.?[0-9]*$/,
        16: /^[0-9A-Fa-f]*\.?[0-9A-Fa-f]*$/
      }
      return patterns[base] || /^$/
    },
    
    getExample(base) {
      const examples = {
        '2': '1010.11',
        '8': '755.4',
        '10': '123.45',
        '16': 'A1B.C'
      }
      return examples[base] || ''
    },
    
    convertToDecimal(value, fromBase) {
      if (!value) return 0
      
      const parts = value.split('.')
      const integerPart = parts[0] || '0'
      const fractionalPart = parts[1] || ''
      
      // è½¬æ¢æ•´æ•°éƒ¨åˆ†
      let decimalValue = parseInt(integerPart, fromBase) || 0
      
      // è½¬æ¢å°æ•°éƒ¨åˆ†
      if (fractionalPart) {
        let fractionalValue = 0
        for (let i = 0; i < fractionalPart.length; i++) {
          const digit = parseInt(fractionalPart[i], fromBase)
          fractionalValue += digit / Math.pow(fromBase, i + 1)
        }
        decimalValue += fractionalValue
      }
      
      return decimalValue
    },
    
    convertFromDecimal(decimalValue, toBase) {
      if (decimalValue === 0) return '0'
      
      const integerPart = Math.floor(Math.abs(decimalValue))
      const fractionalPart = Math.abs(decimalValue) - integerPart
      
      // è½¬æ¢æ•´æ•°éƒ¨åˆ†
      let integerResult = integerPart.toString(toBase)
      if (this.upperCaseHex && toBase === 16) {
        integerResult = integerResult.toUpperCase()
      }
      
      // è½¬æ¢å°æ•°éƒ¨åˆ†
      let fractionalResult = ''
      if (fractionalPart > 0) {
        let fraction = fractionalPart
        const maxPrecision = 10 // é™åˆ¶å°æ•°ä½æ•°
        
        for (let i = 0; i < maxPrecision && fraction > 0; i++) {
          fraction *= toBase
          const digit = Math.floor(fraction)
          fractionalResult += digit.toString(toBase)
          fraction -= digit
        }
        
        if (this.upperCaseHex && toBase === 16) {
          fractionalResult = fractionalResult.toUpperCase()
        }
      }
      
      let result = integerResult
      if (fractionalResult) {
        result += '.' + fractionalResult
      }
      
      // æ·»åŠ å‰ç¼€
      if (this.showPrefix && toBase !== 10) {
        const prefixes = { 2: '0b', 8: '0o', 16: '0x' }
        result = prefixes[toBase] + result
      }
      
      return decimalValue < 0 ? '-' + result : result
    },
    
    getConvertedValue(base) {
      if (!this.inputValue || this.hasError) return ''
      const targetBase = parseInt(base)
      
      if (targetBase === parseInt(this.inputBase)) {
        return this.inputValue
      }
      
      return this.convertFromDecimal(this.decimalValue, targetBase)
    },
    
    generateConversionSteps() {
      const steps = []
      const fromBase = parseInt(this.inputBase)
      
      if (fromBase !== 10) {
        steps.push(`æ­¥éª¤1: å°† ${this.inputValue}(${this.getBaseName(this.inputBase)}) è½¬æ¢ä¸ºåè¿›åˆ¶`)
        
        const parts = this.inputValue.split('.')
        const integerPart = parts[0] || '0'
        const fractionalPart = parts[1] || ''
        
        // æ•´æ•°éƒ¨åˆ†è½¬æ¢æ­¥éª¤
        if (integerPart !== '0') {
          let calculation = ''
          for (let i = 0; i < integerPart.length; i++) {
            const digit = integerPart[integerPart.length - 1 - i]
            const power = i
            if (i > 0) calculation += ' + '
            calculation += `${digit} Ã— ${fromBase}^${power}`
          }
          steps.push(`æ•´æ•°éƒ¨åˆ†: ${calculation} = ${parseInt(integerPart, fromBase)}`)
        }
        
        // å°æ•°éƒ¨åˆ†è½¬æ¢æ­¥éª¤
        if (fractionalPart) {
          let calculation = ''
          for (let i = 0; i < fractionalPart.length; i++) {
            const digit = fractionalPart[i]
            const power = -(i + 1)
            if (i > 0) calculation += ' + '
            calculation += `${digit} Ã— ${fromBase}^${power}`
          }
          steps.push(`å°æ•°éƒ¨åˆ†: ${calculation}`)
        }
        
        steps.push(`ç»“æœ: ${this.decimalValue}(åè¿›åˆ¶)`)
      }
      
      return steps
    },
    
    async copyToClipboard(text) {
      try {
        const success = await ClipboardUtil.copyText(text)
        if (success) {
          this.$message.success('å·²å¤åˆ¶åˆ°å‰ªè´´æ¿ï¼')
        } else {
          this.$message.error('å¤åˆ¶å¤±è´¥ï¼Œè¯·æ‰‹åŠ¨å¤åˆ¶ï¼')
        }
      } catch (error) {
        this.$message.error('å¤åˆ¶å¤±è´¥: ' + error.message)
      }
    },
    
    useAsInput(base, value) {
      this.inputBase = base
      this.inputValue = value.replace(/^0[box]/, '') // ç§»é™¤å‰ç¼€
      this.validateInput()
    },
    
    setQuickExample(example) {
      this.inputBase = example.base
      this.inputValue = example.value
      this.validateInput()
    }
  }
}
</script>

